const detail = () => import('@/components/Detail')
const index = () => import('@/components/Index')
const NotFound = () => import('@/components/404.vue')

const routers = [
  {
    path: '/',
    name: 'index',
    component: index
  },
  {
    path: '/detail/:uuid',
    name: 'detail',
    component: detail
  },
  {
    path: '*',
    component: NotFound
  }
]
export default routers